
import React, { Component } from 'react';
class Banner extends Component {
  constructor(props) {
    super(props)
    this.state = {
      name: "USER",
    };
  }
 updateUser= () => {
   this.state.name ="Shubham"
   console.log("this.state.name",this.state.name)
   // to updating the state
  this.setState({name:"Shubham"},()=>{
    console.log("after change",this.state.name)// delay of 13ms
  })
 }
//setstate is a method state is an object
// in mounting phase the first call is constructor


render() {
  return (<div className="jumbotron p-5">
  <h1 className="display-4 banner-head">Hello, {this.state.name}</h1>
  <p className="lead">This is a simple hero unit, a simple jumbotron-style component for calling extra attention to featured content or information.</p>
  <hr className="my-4"/>
  <p>It uses utility classNamees for typography and spacing to space content out within the larger container.</p>
  <p className="lead">
    <a 
    onClick={
      this.updateUser
    }
    className="btn btn-primary btn-lg" href="#"
     role="button">Change User</a>
  </p>
</div>

  );
}
 
  }



export default Banner;