import React from 'react';
const Card = (props) => {
    const {id,name,preview,photos,description,size,isAccessory,brand,price} = props
    return (<div className="card m-2" style={{width: "18%",borderRadius:"8px",overflow:"hidden"}} key={id}>
    <img className="card-img-top" src={preview} alt="Card image cap"/>
    <div className="card-body">
      <h5 className="card-title">{name}</h5>
      <p className="card-text">{brand}</p>
      <a href="#" className="btn btn-primary">ADD TO CART</a>
    </div>
  </div>  );
}
 
export default Card;