import React, { Component, useState } from 'react';
import "./App.css"
import Topbar from "./Conponents/Topbar"
import Banner from './Conponents/Banner';
import Card from './Conponents/Cards';
import Footer from './Conponents/Footer';
import axios from 'axios';
import Search from './Conponents/Search Bar';

class MyApp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      orgproducts: [],
      loader: true,
      cart: [],
      showChild:true,
      counter:"0"
    };
  }

  componentDidMount() {
    axios.get("https://5d76bf96515d1a0014085cf9.mockapi.io/product")
      .then(res => {
        this.setState({ products: res.data, loader: false, orgproducts: res.data })
      })

  }
  //updating phase condition
  shouldComponentUpdate(nextProps, nextState) {
    if (nextState.products.length >= 0) {
      return true
    } else return false
  }

  handleSearch = (value) => {
    const filteredValues = this.state.orgproducts.filter((item) =>
    item.name.toLowerCase().includes(value.toLowerCase()) || 
    item.brand.toLowerCase().includes(value.toLowerCase()))
    // console.log(value)
    this.setState({ products: filteredValues })
    // if(value==null){this.setState({noItems:false})}
    // console.log(this.state.noItems)
    // if(filteredValues.length==0){this.setState({noItems:true})} else{
    // this.setState({products:filteredValues})}
    // if(value==null){this.setState({noItems:false})}
    // console.log(filteredValues)
  }
  render() {
    return (
      <div>
        <Topbar cartCount={this.state.cart.length} />
        <Banner />
        <Search onSearchValue={this.handleSearch} />
        {this.state.showChild?<Child/>:""}
        <button onClick={()=>this.setState({showChild:!this.state.showChild})}>Child SHOW/HIDE</button>

      
      <Counter/>

        {this.state.loader ? (<div style={{
          backgroundColor: "yellowgreen", display: "flex",
          justifyContent: "center", alignItems: "center", width: "100vw", height: "100vh", zIndex: "2", position: "absolute", top: "0"
        }}>L O A D I N G . . .</div>
        ) : (
          <div className="d-flex flex-wrap" id="cardDiv"
            style={{
              backgroundColor: "whitesmoke",
              width: "90%", margin: "0 auto", justifyContent: "center", padding: "50px", borderRadius: "50px"
            }}>
            {this.state.products.map((item, index) => (
              <Card{...item} />))}
          </div>)}

        {/* {this.state.noItems ? (<div style={{ backgroundColor: "yellowgreen", display: "flex",
         justifyContent: "center", alignItems: "center", width: "40%", height: "10%", zIndex: "2",position:"absolute",top:"80%",left:"30%" }}>No Items Found</div>
        ) : <Card style={{display:"none"}}/>} */}

        <Footer />
      </div>
    );
  }
}

export default MyApp
class Child extends Component {
  constructor(props){
    super(props)
  }
  componentWillUnmount(){
    console.log("You are about to close this component")
  }
  render(){
  return <h1>Hello Child </h1>
}
}

const Counter = () => {
  let[user,setUser]=useState(0)
  return ( <div className="d-flex" style={{width:"40%", justifyContent:"space-between", margin:"0 auto"}}>
  <button onClick={()=>setUser(++user)}>+</button>
  <h1>Counter = {user}</h1>
  <button onClick={()=>{if(user!=0){setUser(--user)}}}>-</button>
</div>  );
}
 
